import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import UserList from "./components/UserList";
import UserForm from "./components/UserForm";
import LongMenu from "./components/LongMenu";
import Footer from "./components/Footer";
import ButtonAppBar from "./components/Appbar";
import {CardContent, createMuiTheme} from "@material-ui/core";
import primaryColour from "@material-ui/core/colors/lightGreen";
import MuiThemeProvider from "@material-ui/core/es/styles/MuiThemeProvider";
import TransferList from "./components/TransferList";

const theme = createMuiTheme({
    palette: {
        primary: primaryColour,
        secondary: {
            main: '#f44336',
        },
    },
});

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [
        {id: 1, name: "miguel", email: "miguelghz@miguelgomez.io"},
        {id: 2, name: "test", email: "test@test.es"}
      ]
    };
    }

    handleOnAddUser (event) {
      event.preventDefault();
      let user = {
        name: event.target.name.value,
        email: event.target.email.value
      };
      this.setState({
        users: this.state.users.concat([user])
      });
    }

    render() {
      return (
          <MuiThemeProvider theme={theme}>

          <div>
              <header>
                  <ButtonAppBar name="asa"/>

              </header>
              <TransferList/>
              <Footer/>
          </div>
          </MuiThemeProvider>
      );
    }

}
class ShoppingList extends React.Component {
  render() {
    return (
        <div className="shopping-list">
          <h1>Shopping List for {this.props.name}</h1>
          <ul>
            <li>Instagram</li>
            <li>WhatsApp</li>
            <li>Oculus</li>
          </ul>
        </div>
    );
  }
}
export default App;
