import {Component} from "react";
import React from "react";
import PropTypes from "prop-types";
import {createMuiTheme, withStyles} from "@material-ui/core";
import BROWN from '@material-ui/core/colors/brown';
import purple from "@material-ui/core/colors/purple";
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import Icon from '@material-ui/core/Icon';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import { withTheme } from '@material-ui/core/styles';

const color = BROWN[500];

const styles = {
};

class Footer extends React.Component {
    state = {
        value: 'recents',
    };

    render() {
        const { classes } = this.props;
        const { value } = this.state;
//"w3-center w3-black w3-padding-64 w3-hover-opacity-off
        return (
            <footer className={classes.root} color="inherit" >
                <div>
                    <br/>
                </div>
                <BottomNavigation value={value} onChange={this.handleChange} color={classes.root}>
                    <BottomNavigationAction label="Recents" value="recents" icon={<RestoreIcon />} />
                    <BottomNavigationAction label="Favorites" value="favorites" icon={<FavoriteIcon />} />
                    <BottomNavigationAction label="Nearby" value="nearby" icon={<LocationOnIcon />} />
                </BottomNavigation>

                <a href="#home" className="primary w3-button ">
                    <i className="fa fa-home "/>Arma Tu Equipo</a>

            </footer>

    );
    }

    handleChange = (event, value) => {
        this.setState({ value });
    };
}

Footer.propTypes = {
    classes: PropTypes.object.isRequired,
};

let componentType = withStyles(styles)(Footer);
export default withTheme() (componentType);

